html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  /* Позиционирование */

  /* Блочная модель */
  min-width: 320px;
  margin: 0;
  padding: 0;

  /* Типографика */
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-family: "PT Mono", "Courier New", monospace;
  color: #666666;

  /* Оформление */
  background-color: #ffffff;

  /* Анимация */

  /* Разное */
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}
