.nav {
  display: flex;

  max-width: 377px;
  min-height: 52px;

  @include outline;
}

.nav__logo {
  display: flex;

  min-width: 202px;
  height: 52px;

  @include outline;

  &:hover .nav__logo-smart {
    fill: white;
  }
}

.nav__logo-img {
  display: block;
  min-width: 52px;
  height: 52px;

  @include outline;
}

.nav__logo-box {
  width: 100%;
  margin-left: 16px;
}

.nav__logo-headline {
  margin: 0;

  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: white;
}

.nav__logo-smart {
  fill: $gray;
}

.nav__menu {
  min-width: 77px;
  height: 52px;
  margin-left: 98px;

  background-color: transparent;
  background-image: url("../img/bg-menu.svg");
  background-repeat: no-repeat;
  background-size: 37px 13px;
  background-position: center;

  cursor: pointer;

  @include outline;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

