@font-face {
  font-family: "PT Mono";
  src: local("PT Mono Regular"), local("PTMono-Regular"), url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: local("Roboto Condensed Regular"), local("RobotoCondensed-Regular"), url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: local("Roboto Condensed Bold"), local("RobotoCondensed-Bold"), url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  /* Позиционирование */
  /* Блочная модель */
  min-width: 320px;
  margin: 0;
  padding: 0;
  /* Типографика */
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  font-family: "PT Mono", "Courier New", monospace;
  color: #666666;
  /* Оформление */
  background-color: #ffffff;
  /* Анимация */
  /* Разное */ }

a {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<< header >>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
.header {
  margin: 0 auto;
  outline: 1px solid red;
  width: 100%;
  background-color: black; }

.header__box {
  margin: 0 auto;
  outline: 1px solid red;
  display: flex;
  max-width: 1461px;
  padding: 33px 25px 0; }

/* >>>>>>>>> */
@media (max-width: 1024px) {
  .header {
    background-color: green; } }

/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<< mobile >>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
@media (max-width: 750px) {
  .header__box {
    padding-left: 17px;
    padding-right: 17px; } }

.nav {
  display: flex;
  max-width: 377px;
  min-height: 52px;
  outline: 1px solid red; }

.nav__logo {
  display: flex;
  min-width: 202px;
  height: 52px;
  outline: 1px solid red; }
  .nav__logo:hover .nav__logo-smart {
    fill: white; }

.nav__logo-img {
  display: block;
  min-width: 52px;
  height: 52px;
  outline: 1px solid red; }

.nav__logo-box {
  width: 100%;
  margin-left: 16px; }

.nav__logo-headline {
  margin: 0;
  font-size: 25px;
  line-height: 29px;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: white; }

.nav__logo-smart {
  fill: #788891; }

.nav__menu {
  min-width: 77px;
  height: 52px;
  margin-left: 98px;
  background-color: transparent;
  background-image: url("../img/bg-menu.svg");
  background-repeat: no-repeat;
  background-size: 37px 13px;
  background-position: center;
  cursor: pointer;
  outline: 1px solid red; }
  .nav__menu:hover {
    opacity: 0.8; }
  .nav__menu:active {
    opacity: 0.6; }

