.contacts {
  display: flex;
  align-items: center;
  width: 75%;
  min-height: 52px;
  outline: 1px solid red; }

.contacts__email,
.contacts__tel {
  display: flex;
  align-items: center;
  min-width: 155px;
  height: 52px;
  margin: 0;
  margin-left: auto;
  padding: 0 10px;
  line-height: 18px;
  color: white;
  outline: 1px solid red; }
  .contacts__email:hover,
  .contacts__tel:hover {
    text-decoration: underline;
    text-decoration-color: #00b2ff; }
  .contacts__email:hover .contacts__email-svg,
  .contacts__email:hover .contacts__tel-svg,
  .contacts__tel:hover .contacts__email-svg,
  .contacts__tel:hover .contacts__tel-svg {
    fill: white; }
  .contacts__email:active,
  .contacts__tel:active {
    opacity: 0.8; }
  .contacts__email:active .contacts__email-svg,
  .contacts__email:active .contacts__tel-svg,
  .contacts__tel:active .contacts__email-svg,
  .contacts__tel:active .contacts__tel-svg {
    fill: #00b2ff; }

.contacts__tel {
  min-width: 227px;
  margin-left: 68px; }

.contacts__email-svg,
.contacts__tel-svg {
  fill: #00b2ff; }

.contacts__email-text {
  padding-left: 20px; }

.contacts__tel-text {
  padding-left: 16px; }

/* --------- btn ----------*/
.contacts__btn {
  display: block;
  min-width: 180px;
  margin: 0;
  margin-left: 40px;
  padding: 13.5px 0;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: white;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer; }
  .contacts__btn:hover {
    opacity: 0.8; }
  .contacts__btn:active {
    opacity: 0.6;
    color: #00b2ff; }

/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<< desktop-help-1 >>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
@media (max-width: 1165px) {
  .contacts__tel {
    margin-left: auto; } }

/* <<<<<<<<<<<<<<<<<<<<<<<<<<<<<< desktop-help-2 >>>>>>>>>>>>>>>>>>>>>>>>>>>>>> */
@media (max-width: 1109px) {
  .contacts__btn {
    margin-left: auto; } }

